@media (max-width: 1199px) {
  .date__month,
.date__year {
    font-size: 10px;
    line-height: 15px;
  }
}

.date__month {
  grid-area: month;
}

.date__year {
  grid-area: year;
}

.time {
  color: #ffffff;
  display: grid;
  grid-template-rows: 0.2fr 0.3fr;
  grid-template-columns: 0.5fr;
}
@media (max-width: 1199px) {
  .time {
    margin-top: -5px;
  }
}
@media (max-width: 767px) {
  .time {
    margin-top: -4px;
    max-width: 125px;
  }
}

.time__title {
  font-size: 18px;
  line-height: 27px;
  font-weight: 400;
  border-bottom: 1px #ffffff solid;
}
@media (max-width: 1199px) {
  .time__title {
    font-size: 12px;
    line-height: 18px;
  }
}

.time__watch {
  font-size: 40px;
  line-height: 59px;
  font-weight: 700;
}
@media (max-width: 1199px) {
  .time__watch {
    font-size: 24px;
    line-height: 36px;
  }
}

.coaches {
  display: block;
  padding: 58px 0 140px 0;
}
@media (max-width: 1199px) {
  .coaches {
    padding: 48px 0 140px 0;
  }
}
@media (max-width: 767px) {
  .coaches {
    padding: 10px 0 140px 0;
  }
}

@media (max-width: 1199px) {
  .coaches h2 {
    text-align: center;
  }
}

.coach {
  position: relative;
  padding-top: 113.0769230769%;
}
@media (min-width: 1200px) {
  .coach:hover .coach__descpiption {
    flex-direction: column;
    background-color: rgba(16, 38, 84, 0.85);
  }
  .coach:hover .coach__subtitle {
    opacity: 1;
  }
  .coach:hover .coach__list {
    opacity: 1;
  }
}
@media (max-width: 1199px) {
  .coach {
    padding-top: 109.7014925373%;
  }
}
@media (max-width: 767px) {
  .coach {
    padding-top: 121.2389380531%;
  }
}

.coach__foto,
.coach__descpiption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.coach__foto img {
  object-fit: cover;
}
@media (max-width: 1199px) {
  .coach__foto img {
    width: 268px;
    height: 294px;
  }
}

.coach__descpiption {
  color: #ffffff;
  padding: 52px 45px 22px 43px;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  overflow-y: auto;
  background-color: transparent;
}
@media (max-width: 767px) {
  .coach__descpiption {
    padding: 42px 23px 23px 22px;
  }
}

.coach__subtitle {
  color: #b6bdcc;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 30px;
  margin-top: -4px;
  opacity: 0;
}

.coach__list {
  list-style-type: disc;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  margin-left: 28px;
  opacity: 0;
}

.coach__item {
  margin-bottom: 14px;
}

.coach__click .coach__descpiption {
  flex-direction: column;
  background-color: rgba(16, 38, 84, 0.85);
}
.coach__click .coach__subtitle {
  opacity: 1;
}
.coach__click .coach__list {
  opacity: 1;
}

